<template>
	<!-- 通用顶部栏页面 -->
	 <el-row class="fiex">
		<el-col :span="7">
		 				 <div class="header-left">
		 				 <img src="../../assets/user/Shape.png" alt="" />
		 				 <span>{{navs.headerTitle}}</span>
		 				 </div>
		 </el-col>
	<!-- 	 <el-col :span="12">
		 <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="go">
		   <el-menu-item :index="index.toString()" v-for="(item,index) in navs.headerList" :key="index">
			   {{item.title}}
		   </el-menu-item>
		 </el-menu>
		
		 </el-col> -->
		 <el-col :span="2"  v-for="(item,index) in navs.headerList" :key="index" ><div class="grid-content" @click="go(item)">{{item.title}}</div></el-col>
		 
		<el-col :span="2">
			<headerR></headerR>
		</el-col>
			<!-- <el-col :span="7">
				 <div class="header-left">
				 <img src="../../assets/user/Shape.png" alt="" />
				 <span>{{navs.headerTitle}}</span>
				 </div>
		 </el-col>
			 <el-col :span="2"  v-for="(item,index) in navs.headerList" :key="index" ><div class="grid-content" @click="go(item)">{{item.title}}</div></el-col>
		 <el-col :span="2">
			 <div class="header-right">
			 <span>你好，我是一只小小小鸟</span>
			 <img src="../../assets/user/xinxi.png" alt="" />
			 </div>
		 </el-col> -->
    </el-row>
</template>

<script>
import navs from '../../assets/config/nav.json'
import headerR from '../common/headerR.vue'
import other from '../../../static/config/other.json'
export default {
  data(){
	return {
		navs:{},
		activeIndex: '0',
	}  
  },
  components:{
	  headerR
  },
  mounted() {
	
  	this.navs=navs
  },
  methods:{
	  go(item){
		console.log(item)
		  switch (item.title) {
		   case "首页":
		     this.$router.push("/student/home");
		     break;
		   case "我的课程":
		     this.$router.push("/student/myClass");
		     break;
			// case "推演培训":
			//  // this.$router.push("/student/deduction");
			//  window.open(other.url);
			//  break;
			// case "数据仿真":
			//    this.$router.push("/student/training");
			//    break;
			case "我的考试":
			   this.$router.push("/student/exam");
			   break;
			case "学习笔记":
			   this.$router.push("/student/note");
			   break;
			   case "厂房综合":
			   this.$router.push("/three");
			   break;
			   case "cesium":
			   this.$router.push("/student/cesium");
			   break;
		  }
			this.$emit('select',{item:item})
	  }
  }
};
</script>

<style  scoped lang="less">
@import '../../assets/css/header.css';
</style>



