import request from "../base";
import config from '../../../static/config'
export  function Getuser(params) {
  return request.put('/user',{params});
}

//登出 
export  function Loginout(params) {
  return request.get(config.CO_URL+"/co/auth/logout?access_token="+params.access_token+"&flag=1&state=0&callBackUrl="+config.BACK_URL);
}

//个人信息
export  function getInfo(params) {
  return request.get(config.INFO+"/co/user/info");
}

//我的考试列表
export  function getEaxm(params) {
  return request.post(config.EXAM+"/api/student/exam/paper/pageList?userId=10004&limit="+params.limit+"&page="+params.page+"&name="+params.name+"&orderByCreateTimeAsc="+params.orderByCreateTimeAsc);
}

//获取试卷信息
export  function getEaxmInfo(params) {
  return request.post(config.EXAM+"/api/student/exam/paper/select/"+params.id);
}

// // 系统登陆
// export  function Login(params) {
//   return request.post(config.SYS+"/api/oauth/loginToken?username="+params.username+"&password="+params.password);
// }


//获取课程列表
export  function getSubject(params) {
  return request.get(config.EXAM+"/api/student/subject/page?limit="+params.limit+"&page="+params.page+"&keyword="+params.keyword+"&orderByCreateTimeAsc="+params.orderByCreateTimeAsc);
}


// 提交试卷
export  function subPage(params) {
  return request.post(config.EXAM+"/api/student/paper/answer/answerSubmit/10004",params);
}

// 课程二级页面
export  function classS(params) {
  return request.get(config.EXAM+"/api/student/subject/"+params.id);
}

// 我的笔记
export  function myNote(params) {
  return request.get(config.NOTE+"/deduction/SafegdBusinessNotes/query.do");
}

// 数据仿真
export  function emulation(params) {
  return request.get(config.NOTE+"/emulation/emulation/query.do?search-LIKE-EMU_TITLE_NAME="+params.name);
}

// 数据仿真下载
export  function dowlond(params) {
  return request.get(config.NOTE+"/emulation/emulation/download?emulationId="+params.id);
}

// 删除笔记
export  function removeById(params) {
  return request.delete(config.NOTE+"/deduction/SafegdBusinessNotes/removeById.do?entityID="+params.id);
}




