<template>
  <el-container>
    <el-header>
      <div class="header-left">
        <img src="../../../assets/user/Shape.png" alt="" />
        <span>保障技术推演培训考试系统</span>
      </div>
      <div class="header-right">
        <img src="../../../assets/user/xinxi.png" alt="" />
        <span>username</span>
      </div>
    </el-header>
    <el-container>
      <el-aside width="210px">
        <div class="user">
          <div class="user-img">
            <svg
              class="icon"
              aria-hidden="true"
              style="width: 60px; height: 57px"
            >
              <use xlink:href="#icon-renwu"></use>
            </svg>
          </div>
          <div class="user-details">
            <p>
              <span>张三</span>
              <span>,欢迎您!</span>
            </p>
            <p>
              <span>角色</span>
              <span>系统管理员</span>
            </p>
          </div>
        </div>
        <div>
          <el-menu
            default-active="2"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            text-color="#fff"
            active-text-color="#fff"
          >
            <el-menu-item
              :index="index"
              v-for="(items, index) in listArr"
              :key="index"
              @click="handChangePage(items, index)"
              :class="[idx === index ? 'active' : '']"
            >
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-peizhiguanli"></use>
              </svg>
              <span>{{ items }}</span>
            </el-menu-item>
          </el-menu>
        </div>
      </el-aside>
      <el-main>
        <List :idxName="idxName"></List>
        <router-view></router-view>
        <img src="" alt="" />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import List from "../../../components/nav/miaobaoxie.vue";
export default {
  name: "User",
  components: {
    //面包屑
    List,
  },
  data() {
    return {
      idx: 2,
      listArr: [
        "系统配置",
        "数据库管理",
        "用户管理",
        "配置管理",
        "日志管理",
        "课程管理",
        "推演管理",
        "仿真管理",
        "考试管理",
        "日志管理",
      ],
      idxName: "用户管理",
    };
  },
  methods: {
    handChangePage(item, index) {
      this.idx = index;
      this.idxName = item;
      switch (item) {
        case "用户管理":
          this.$router.push("/list/user");
          break;
        case "系统配置":
          this.$router.push("/list/system");
          break;
      }
    },
  },
};
</script>

<style  scoped>
.el-header {
  display: flex;
  position: relative;
  width: 100%;
  height: 61px;
  line-height: 61px;
  background: linear-gradient(156deg, #002454 0%, #235ebe 0%, #154794 92%);
}
.el-aside {
  color: #333;
  line-height: 200px;
  width: 100%;
  height: calc(100vh - 30px);
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  width: 100%;
}
.user {
  display: flex;
  align-content: center;
  align-items: center;
  width: 208px;
  background-color: #2f404f;
}
.user-img {
  display: flex;
  width: 60px;
  height: 57px;
  margin-left: 5px;
}
.user-details {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  line-height: 10px;
  text-align: center;
  color: #fff;
}
.user-details p:nth-child(1) span:nth-child(1) {
  color: #067eff;
}
.user-details p:nth-child(2) span:nth-child(1) {
  display: inline-block;
  width: 34px;
  height: 16px;
  background: #067eff;
  margin-left: 11px;
  border-radius: 2px;
  font-size: 10px;
  line-height: 16px;
}
.el-menu {
  background: url("../../../assets/user/ditu.png") no-repeat;
  background-size: 100% 100%;
  height: calc(100vh - 100px);
  border: none !important;
}
.el-menu span {
  margin-left: 20px;
}
/* 悬浮后后的导航栏的颜色 */
.el-submenu__title:hover,
.el-menu-item:focus,
.el-menu-item:hover {
  background-color: #4a90e2 !important;
}
/* 选中后的导航栏的颜色 */
.active {
  background: #4a90e2 !important;
}
.header-left {
  display: flex;
  align-items: center;
  justify-items: center;
}
.header-left img {
  margin-left: 23px;
}
.header-left span {
  margin-left: 12px;
  font-size: 20px;
  font-weight: 400;
  color: #fff;
}
.header-right {
  display: flex;
  align-items: center;
  justify-items: center;
  position: absolute;
  right: 30px;
  color: #fff;
}
.header-right img {
  width: 23px;
  margin-right: 15px;
}
.header-right span {
  font-size: 12px;
}
</style>




