/**
 * @param {array}
 * @returns {Boolean}
 */
export function changeAbcd(data,item,key){
	let da
		let items={"contentArray":[],"content":null,"itemOrder":'',"completed":false,questionId:'',type:''}
	if(key==1){
		if(data.answor==''){
			return {"contentArray":[],"content":null,"itemOrder":'',"completed":false,questionId:'',type:1}
		}
		da=data.answor.split('.')
		items.type=1
			items.questionId=da[0]
			items.itemOrder=da[0]
			items.completed=true
			if(da[1]==0){
				items.content='A'
			} else if(da[1]==1){
				items.content='B'
			}else if(da[1]==2){
				items.content='C'
			}else if(da[1]==3){
				items.content='D'
			}
	}else if(key==2){
		if(data.answor.length==0){
			return {"contentArray":[],"content":null,"itemOrder":'',"completed":false,questionId:'',type:2}
		}
		data.answor.forEach(item1=>{
			items.type=2
			da=item1.split('.')
			items.questionId=da[0]
			items.itemOrder=da[0]
			items.completed=true
			if(da[1]==0){
				items.contentArray.push('A')
			} else if(da[1]==1){
				items.contentArray.push('B')
			}else if(da[1]==2){
				items.contentArray.push('C')
			}else if(da[1]==3){
				items.contentArray.push('D')
			}else if(da[1]==4){
				items.contentArray.push('E')
			}else if(da[1]==5){
				items.contentArray.push('F')
			}else if(da[1]==6){
				items.contentArray.push('G')
			}
		})
	}else if(key==4){
		if(data.answor==''){
			return {"contentArray":[],"content":null,"itemOrder":'',"completed":false,questionId:'',type:4}
		}
		items.completed=true
		items.type=4
		items.content=data.answor
	} else if(key==3){
		if(data.answor==''){
			return {"contentArray":[],"content":null,"itemOrder":'',"completed":false,questionId:'',type:3}
		}
		da=data.answor.split('.')
			items.questionId=da[0]
			items.itemOrder=da[0]
			items.completed=true
			items.type=3
			if(da[1]==0){
				items.content='A'
			} else if(da[1]==1){
				items.content='B'
			}
	}
	// console.log("最终:",items)
	return items
}
