import Vue from 'vue'
import Router from 'vue-router'
import User from '../layout/components/system/User'
import Index from '../layout/components/student/Index.vue'
// import Login from '../layout/Login.vue'
Vue.use(Router)

export default new Router({
  routes: [
	// {
	// 	path: '/',
	// 	name: 'Login',
	// 	component: Login,
	// },
	{
		path: '/three',
		name: 'three',
		component: ()=> import('../layout/components/student/list/three.vue'),
	},
	{
	  path: '/',
	  name: 'Index',
	  component: Index,
		redirect:'/three',
	  children:[
		 {
		     path: '/student/home',
		 	name: 'home',
		 	component: ()=> import('../layout/components/student/list/home.vue'),
		 },
		//  {
		// 	path: '/student/three',
		// 	name: 'three',
		// 	component: ()=> import('../layout/components/student/list/three.vue'),
		// },
		 {
		   path: '/student/myClass',
		   name: 'myClass',
		   component: ()=> import('../layout/components/student/list/myClass.vue'),
		 },
		 {
		   path: '/student/exam',
		   name: 'exam',
		   component: ()=> import('../layout/components/student/list/exam.vue'),
		 },
		 {
		   path: '/student/note',
		   name: 'note',
		   component: ()=> import('../layout/components/student/list/note.vue'),
		 },
	    {
	      path: '/student/userInfo',
	      name: 'userInfo',
	      component: ()=> import('../layout/components/student/list/userInfo.vue'),
	    },
			{
			  path: '/student/pwdInfo',
			  name: 'pwdInfo',
			  component: ()=> import('../layout/components/student/list/pwdInfo.vue'),
			},
		{
		  path: '/student/training',
		  name: 'training',
		  component: ()=> import('../layout/components/student/list/training.vue'),
		},
		{
		  path: '/student/deduction',
		  name: 'deduction',
		  component: ()=> import('../layout/components/student/list/deduction.vue'),
		},
		{
		  path: '/student/sec/classSec',
		  name: 'classSec',
		  component: ()=> import('../layout/components/student/secList/classSec.vue'),
		},
		{
		  path: '/student/sec/videoPSec',
		  name: 'videoPSec',
		  component: ()=> import('../layout/components/student/secList/videoPSec.vue'),
		},
		{
		  path: '/student/sec/deduceSec',
		  name: 'deduceSec',
		  component: ()=> import('../layout/components/student/secList/deduceSec.vue'),
		},
		{
		  path: '/student/sec/examSec',
		  name: 'examSec',
		  component: ()=> import('../layout/components/student/secList/examSec.vue'),
		},
		{
		  path: '/student/sec/noteP',
		  name: 'noteP',
		  component: ()=> import('../layout/components/student/secList/noteP.vue'),
		}
	  ]
	}
  ]
})



