<style  scoped>
.title {
  display: flex;
  justify-items: center;
  align-items: center;
  height: 38px;
  background: #ffffff;
  border: 1px solid #eaeaea;
  margin-bottom: 14px;
}
.el-breadcrumb {
  margin-left: 10px;
}
</style>



