<template>
			<div class="header-right">
			<el-dropdown>
					 <span class="rname">{{userInfo.name}}<img src="../../assets/user/xinxi.png" alt="" class="nameImg"/></span>
					 <el-dropdown-menu slot="dropdown" @command="go">
					     <el-dropdown-item v-for="(item,index) in navs" :command="item.title" :key="index"><div @click="go(item)">{{item.title}}</div></el-dropdown-item>
					  </el-dropdown-menu>
			</el-dropdown>
			</div>
</template>

<script>
import navs from '../../assets/config/navr.json'
import {Loginout} from '../../api/apis/user.js'
export default {
  data(){
	return {
		navs:[],
		userInfo:''
	}  
  },
  mounted() {
  	this.navs=navs.navList
	let that=this
	setTimeout(function(){
		that.userInfo=JSON.parse(localStorage.getItem('userInfo'))
	},1000)
  },
  methods:{
	  go(item){
		  console.log(item)
		  switch(item.title){
			  case '退出':
				Loginout({access_token:localStorage.getItem('access_token')}).then((res)=>{
					window.location.href=res.data
				}).catch(err=>{
				})
				// this.$router.push('/');
				// window.location.href="http://192.168.101.152:7001/co/auth/logout?access_token="+localStorage.getItem('access_token')+"&flag=1&state=0&callBackUrl=http://192.168.101.152:81/"
				break;
			  case '密码修改':
				this.$router.push('/student/pwdInfo');
				break;
			  case '个人信息':
				this.$router.push('/student/userInfo');
				break;
		  }
	  }
  }
};
</script>

<style  scoped lang="less">
@import '../../assets/css/header.css';
</style>



